import React, { useState, useCallback } from 'react';

import { FormHelperText, styled, useTheme } from '@mui/material';
import { Editor as EditorComponent } from '@tinymce/tinymce-react';
import { Editor } from 'tinymce';

export type EditorWrapperProps = {
  errorText?: string;
  onBlur: () => void;
  onChange: (html: string) => void;
  value: string;
};

const Container = styled('div')(({ theme }) => ({
  '&.error .tox': {
    border: `solid 1px ${theme.palette.error.main}`,
  },
}));

export const EditorWrapper: React.FC<EditorWrapperProps> = ({
  errorText,
  onBlur,
  onChange,
  value,
}) => {
  const theme = useTheme();

  const [editor, setEditor] = useState<Editor>();
  const handleChange = useCallback(() => {
    if (editor !== undefined) {
      onChange(editor.getContent());
    }
  }, [editor, onChange]);

  return (
    <Container className={errorText !== undefined ? 'error' : undefined}>
      <EditorComponent
        /** free tiny.cloud account mrvisser@gmail.com */
        apiKey="lqdxhe0s1vh85uk1ft713xbc1t39xpqugkonxscjeu9azh4z"
        init={{
          branding: false,
          content_style: `
            body { 
              color: ${theme.palette.text.primary};
              font-family: ${theme.typography.fontFamily ?? 'sans-serif'};
              font-weight: ${theme.typography.fontWeightRegular ?? '400'};
              font-size: 1rem;
              line-height: 1.5;
            }

            p {
              margin: 0;
            }
          `,
          init_instance_callback: editor => setEditor(editor),
          max_height: 500,
          menubar: false,
          plugins: [
            'autolink',
            'autoresize',
            'code',
            'fullscreen',
            'image',
            'link',
            'lists',
            'table',
            'advlist',
          ],
          toolbar:
            'bold italic bullist numlist table image | fullscreen | removeformat code',
        }}
        onBlur={onBlur}
        onEditorChange={() => handleChange()}
        onKeyUp={() => handleChange()}
        value={value}
      />
      {errorText !== undefined ? (
        <FormHelperText error>{errorText}</FormHelperText>
      ) : undefined}
    </Container>
  );
};
