import React, { useEffect, useMemo, useRef, useState } from 'react';

import type { CreateTourRequest } from '@enactive-ac/api/src/domain/CreateTour';
import type { Site } from '@enactive-ac/api/src/domain/Site';
import type { Tour, TourMatch } from '@enactive-ac/api/src/domain/Tour';
import type { TourStatus } from '@enactive-ac/api/src/domain/TourStatus';
import type { TourStep } from '@enactive-ac/api/src/domain/TourStep';
import {
  Add,
  ArrowDownward,
  ArrowUpward,
  Delete,
  Error,
  ExpandMore,
  SpeakerNotes,
} from '@mui/icons-material';
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  FormControlLabel,
  IconButton,
  styled,
  Stack,
  Switch,
  ToggleButton,
  ToggleButtonGroup,
  useTheme,
  Box,
  Typography,
} from '@mui/material';
import {
  Formik,
  Field,
  FieldArray,
  Form,
  FormikProps,
  FormikErrors,
  FieldArrayRenderProps,
} from 'formik';
import { TextField } from 'formik-mui';
import { SchemaOf } from 'yup';

import { ManagementService } from '../services/ManagementService';
import yup from '../validation/yup';
import { Button } from './Button';
import { ConditionsField } from './ConditionsField';
import { DialogDrawer } from './DialogDrawer';
import { EditorWrapper } from './EditorWrapper';
import { FirstAction } from './FirstAction';
import { Flex, FlexItem } from './Flex';

export type EditTourDrawerProps = {
  managementService: ManagementService;
  onClose?: () => void;
  onExited?: () => void;
  onSave?: (tour: Tour) => void;
  open: boolean;
  site: Site;
  tour?: Tour;
};

const SelectorTypeContainer = styled(FlexItem)(({ theme }) => ({
  margin: theme.spacing(0.5),
}));

const StepAccordionSummary = styled(AccordionSummary)(({ theme }) => ({
  '& > div.MuiAccordionSummary-content': {
    marginRight: theme.spacing(2),
    minWidth: 0,
  },
}));

const selectorSchema: SchemaOf<Required<TourStep['selector']>> = yup
  .object()
  .shape({
    type: yup.mixed<'css' | 'xpath'>().oneOf(['css', 'xpath']).default('css'),
    value: yup
      .string()
      .when('type', {
        is: 'xpath',
        otherwise: yup.string().validCssSelector(),
        then: yup.string().validXpathSelector(),
      })
      .default(''),
  });

const stepSchema: SchemaOf<Omit<TourStep, 'id' | 'buttons'>> = yup
  .object()
  .shape({
    html: yup
      .string()
      .required('Message is required')
      .validNonEmptyHtml({ empty: 'Message requires some text' }),
    mask: yup.boolean().required(),
    selector: selectorSchema.notRequired(),
  });

const tourMatchSchema: SchemaOf<TourMatch> = yup.object().shape({
  data: yup.object(),
  jsonpath: yup.string().default('').validJsonPathFilter(),
});

const tourSchema: SchemaOf<
  Omit<Tour, 'id' | 'steps'> & { steps: Omit<TourStep, 'id' | 'buttons'>[] }
> = yup.object().shape({
  launch: tourMatchSchema.nullable(true).optional(),
  match: tourMatchSchema,
  name: yup.string().required('Name is required'),
  status: yup
    .mixed<'draft' | 'published'>()
    .oneOf(['draft', 'published'])
    .default('draft'),
  steps: yup.array().of(stepSchema),
});

export const EditTourDrawer: React.FC<EditTourDrawerProps> = ({
  managementService,
  onClose = () => undefined,
  onExited = () => undefined,
  onSave = () => undefined,
  open,
  site,
  tour,
}) => {
  const theme = useTheme();
  const formRef = useRef<FormikProps<CreateTourRequest['tour']>>(null);
  const [formTour, setFormTour] = useState(() => mkTourEdit(tour));
  const isEdit = useMemo(() => (tour?.id ?? '') !== '', [tour]);

  const [changeStatus, setChangeStatus] = useState<TourStatus>();
  const [stepExpansionState, setStepExpansionState] = useState<
    Record<number, boolean>
  >({});

  useEffect(() => {
    setFormTour(mkTourEdit(tour));
    setStepExpansionState({});
  }, [tour]);

  function handleCreateStep(
    formik: FormikProps<CreateTourRequest['tour']>,
    fieldArray: FieldArrayRenderProps,
  ) {
    setStepExpansionState({
      [formik.values.steps.length]: true,
    });
    fieldArray.push(mkTourEditStep());
  }

  function doSubmit(status?: TourStatus) {
    setChangeStatus(status);
    if (formRef.current !== null) {
      void formRef.current.submitForm();
    }
  }

  async function handleDelete() {
    if (tour !== undefined) {
      await managementService.deleteTour(site.id, tour.id);
      onClose();
    }
  }

  return (
    <DialogDrawer
      actions={
        <>
          <Button onClick={() => onClose()} size="large">
            Cancel
          </Button>
          <Button onClick={() => doSubmit('draft')} size="large">
            {!isEdit || formTour?.status === 'draft'
              ? 'Save draft'
              : 'Save and unpublish'}
          </Button>
          <Button
            onClick={() => doSubmit('published')}
            size="large"
            variant="contained"
          >
            {!isEdit
              ? 'Publish'
              : formTour?.status === 'draft'
              ? 'Save and publish'
              : 'Save'}
          </Button>
        </>
      }
      onClose={onClose}
      onExited={onExited}
      open={open}
      title={!isEdit ? 'Create tour' : 'Edit tour'}
    >
      <Formik
        enableReinitialize
        initialValues={formTour}
        innerRef={formRef}
        onSubmit={async (tourEdit, actions) => {
          actions.setSubmitting(true);

          // Clean the tour. Namely, remove selector objects whose value are blank
          const toSave: CreateTourRequest['tour'] = {
            ...tourEdit,
            status: changeStatus ?? formTour?.status ?? tourEdit.status,
            steps: tourEdit.steps.map(step => ({
              ...step,
              selector:
                step.selector !== undefined && step.selector.value.trim() === ''
                  ? undefined
                  : step.selector,
            })),
          };

          const result = await (!isEdit
            ? managementService.createTour(site.id, { tour: toSave })
            : managementService.updateTour(site.id, tour?.id ?? '', {
                tour: toSave,
              }));
          actions.setSubmitting(false);
          onSave(result);
        }}
        validateOnChange={false}
        validationSchema={tourSchema}
      >
        {formik => (
          <Form>
            <Typography component="h2" mb={0.75} variant="subtitle1">
              Profile
            </Typography>
            <Field
              component={TextField}
              fullWidth
              helperText="A human-friendly name, for your eyes only"
              label="Tour name"
              name="name"
              placeholder="New account orientation"
              size="medium"
              validate
              variant="outlined"
            />
            <Typography component="h2" mb={0.75} mt={2.5} variant="subtitle1">
              Criteria
            </Typography>
            <Box my={2}>
              <ConditionsField
                FieldProps={{
                  base: {
                    fullWidth: true,
                    label: 'Available when...',
                  },
                }}
                attrName="match"
                formik={formik}
                site={site}
                tour={tour}
              />
            </Box>
            <Box my={2}>
              <FormControlLabel
                control={
                  <Switch
                    checked={formik.values.launch?.jsonpath === 'true'}
                    onChange={() => {
                      if ((formik.values.launch ?? undefined) === undefined) {
                        formik.setFieldValue('launch', { jsonpath: 'true' });
                      } else {
                        formik.setFieldValue('launch', null);
                      }
                    }}
                  />
                }
                label="Automatically launch"
              />
            </Box>
            <FieldArray name="steps">
              {fieldArray => (
                <>
                  <Typography
                    component="h2"
                    mb={0.75}
                    mt={2.5}
                    variant="subtitle1"
                  >
                    <Flex>
                      <FlexItem grow>Steps</FlexItem>
                      <FlexItem>
                        <IconButton
                          color="primary"
                          onClick={() => handleCreateStep(formik, fieldArray)}
                        >
                          <Add />
                        </IconButton>
                      </FlexItem>
                    </Flex>
                  </Typography>
                  {formik.values.steps.length === 0 ? (
                    <Flex justifyContent="center" width="100%">
                      <FirstAction
                        actionsComponent={
                          <Button
                            color="primary"
                            onClick={() => handleCreateStep(formik, fieldArray)}
                            variant="contained"
                          >
                            Create Step
                          </Button>
                        }
                        illustrationComponent={
                          <SpeakerNotes
                            style={{
                              height: theme.typography.h2.fontSize,
                              width: theme.typography.h2.fontSize,
                            }}
                          />
                        }
                        mt={2}
                        style={{ maxWidth: '30rem' }}
                      >
                        A tour step is one message in a sequence of interactive
                        message dialogs that are shown to your users when the
                        tour criteria is met.
                      </FirstAction>
                    </Flex>
                  ) : (
                    <Box sx={{ width: '100%' }}>
                      {formik.values.steps.map((step, i) => (
                        <Accordion
                          key={i}
                          expanded={stepExpansionState[i] ?? false}
                        >
                          <StepAccordionSummary
                            expandIcon={<ExpandMore />}
                            onClick={() =>
                              setStepExpansionState(ses => ({ [i]: !ses[i] }))
                            }
                          >
                            <FlexItem mr={1} width="2rem">
                              <Typography component="h3" variant="body1">
                                {i + 1}.
                              </Typography>
                            </FlexItem>
                            <FlexItem
                              flex="1"
                              minWidth="0"
                              overflow="hidden"
                              textOverflow="ellipsis"
                              whiteSpace="nowrap"
                            >
                              <Typography
                                sx={{
                                  display: 'inline',
                                  opacity: 0.875,
                                }}
                                variant="body2"
                              >
                                {getTextFromHtml(step.html)}
                              </Typography>
                            </FlexItem>
                            {hasInnerStepError(formik.errors, i) ? (
                              <FlexItem
                                color="error.main"
                                mr={1}
                                sx={{ alignItems: 'center', display: 'flex' }}
                              >
                                <Error />
                              </FlexItem>
                            ) : undefined}
                          </StepAccordionSummary>
                          <AccordionDetails>
                            <Flex justifyContent="flex-end">
                              <IconButton
                                disabled={i === 0}
                                onClick={() => {
                                  setStepExpansionState(ses => ({
                                    ...ses,
                                    [i - 1]: ses[i],
                                    [i]: ses[i - 1],
                                  }));
                                  fieldArray.swap(i - 1, i);
                                }}
                                title="Move up"
                              >
                                <ArrowUpward />
                              </IconButton>
                              <IconButton
                                disabled={i === formik.values.steps.length - 1}
                                onClick={() => {
                                  setStepExpansionState(ses => ({
                                    ...ses,
                                    [i + 1]: ses[i],
                                    [i]: ses[i + 1],
                                  }));
                                  fieldArray.swap(i, i + 1);
                                }}
                                title="Move down"
                              >
                                <ArrowDownward />
                              </IconButton>
                              <Box ml={2}>
                                <Button
                                  color="inherit"
                                  onClick={() => {
                                    fieldArray.remove(i);
                                    setStepExpansionState(ses => ({
                                      ...ses,
                                      [i]: false,
                                    }));
                                  }}
                                  startIcon={<Delete />}
                                >
                                  Delete
                                </Button>
                              </Box>
                            </Flex>
                            <Box my={2}>
                              <label>
                                Message content:
                                <EditorWrapper
                                  errorText={
                                    formik.getFieldMeta(`steps[${i}].html`)
                                      .error
                                  }
                                  onBlur={() => {
                                    formik.setFieldTouched(
                                      `steps[${i}].html`,
                                      true,
                                    );
                                  }}
                                  onChange={html => {
                                    formik.setFieldValue(
                                      `steps[${i}].html`,
                                      html,
                                    );
                                  }}
                                  value={formik.values.steps[i].html}
                                />
                              </label>
                            </Box>
                            <Box my={2}>
                              <FormControlLabel
                                control={
                                  <Switch
                                    checked={formik.values.steps[i].mask}
                                    onChange={() => {
                                      formik.setFieldValue(
                                        `steps[${i}].mask`,
                                        !formik.values.steps[i].mask,
                                      );
                                    }}
                                  />
                                }
                                label="Darken the screen"
                              />
                            </Box>
                            <Stack direction="row" my={2}>
                              <FlexItem grow>
                                <Field
                                  component={TextField}
                                  fullWidth
                                  helperText="Use the CSS/XPath toggle to choose a selector format"
                                  label="Attach to element"
                                  name={`steps[${i}].selector.value`}
                                  size="medium"
                                  variant="outlined"
                                />
                              </FlexItem>
                              <SelectorTypeContainer>
                                <ToggleButtonGroup
                                  onChange={() => {
                                    // After the value change happens in the form, set the selector
                                    // field as touched so it revalidates
                                    setTimeout(() =>
                                      formik.setFieldTouched(
                                        `steps[${i}].selector.value`,
                                        true,
                                        true,
                                      ),
                                    );
                                  }}
                                  value={[
                                    formik.values.steps[i].selector?.type ??
                                      'css',
                                  ]}
                                >
                                  <ToggleButton
                                    onClick={() =>
                                      formik.setFieldValue(
                                        `steps[${i}].selector.type`,
                                        'css',
                                      )
                                    }
                                    value="css"
                                  >
                                    CSS
                                  </ToggleButton>
                                  <ToggleButton
                                    onClick={() =>
                                      formik.setFieldValue(
                                        `steps[${i}].selector.type`,
                                        'xpath',
                                      )
                                    }
                                    value="xpath"
                                  >
                                    XPath
                                  </ToggleButton>
                                </ToggleButtonGroup>
                              </SelectorTypeContainer>
                            </Stack>
                          </AccordionDetails>
                        </Accordion>
                      ))}
                    </Box>
                  )}
                </>
              )}
            </FieldArray>
            {isEdit ? (
              <Stack direction="row-reverse" my={2}>
                <Button
                  color="error"
                  onClick={handleDelete}
                  variant="contained"
                >
                  <Delete /> Delete
                </Button>
              </Stack>
            ) : undefined}
          </Form>
        )}
      </Formik>
    </DialogDrawer>
  );
};

function hasInnerStepError(
  tour: FormikErrors<CreateTourRequest['tour']>,
  stepIndex: number,
) {
  if (tour.steps === undefined || typeof tour.steps === 'string') {
    return false;
  } else {
    // If there is any prop with a value, then it's an error
    const step = tour.steps[stepIndex];
    return (
      step !== undefined &&
      Object.values(step).find(s => s !== undefined) !== undefined
    );
  }
}

function getTextFromHtml(html: string): string {
  try {
    const text =
      new DOMParser()
        .parseFromString(html, 'text/html')
        ?.body.textContent?.trim() ?? '';
    return text === '' ? 'No content' : text;
  } catch (err) {
    return 'No content';
  }
}

function mkTourEdit(tour?: Tour): CreateTourRequest['tour'] {
  if (tour === undefined) {
    return {
      match: {
        jsonpath: '',
      },
      name: '',
      status: 'draft',
      steps: [],
    };
  } else {
    const { id: _, ...t } = tour;
    return {
      ...t,
      match: {
        jsonpath: tour.match.jsonpath ?? '',
      },
      steps: tour.steps.map(mkTourEditStep),
    };
  }
}

function mkTourEditStep(step?: TourStep): TourStep {
  if (step === undefined) {
    return {
      html: '',
      id: '',
      mask: false,
      selector: {
        type: 'css',
        value: '',
      },
    };
  } else {
    return step.selector !== undefined
      ? step
      : {
          ...step,
          selector: {
            type: 'css',
            value: '',
          },
        };
  }
}
