import React, { useMemo } from 'react';

import { ButtonProps, Button as MuiButton } from '@mui/material';

export const Button: React.FC<ButtonProps> = ({
  disabled = false,
  key,
  ...buttonProps
}) => {
  const k = useMemo(() => {
    // Workaround: https://github.com/mui-org/material-ui/issues/26251
    if ((key === undefined || key === null) && !disabled) {
      return undefined;
    } else if (key === undefined || key === null) {
      return 'disabled';
    } else if (!disabled) {
      return key;
    } else {
      return `${key}-disabled`;
    }
  }, [disabled, key]);
  return <MuiButton {...buttonProps} key={k} disabled={disabled} />;
};
