import React, { useRef } from 'react';

import type { Site } from '@enactive-ac/api/src/domain/Site';
import type { SiteTheme } from '@enactive-ac/api/src/domain/SiteTheme';
import { Box, TextFieldProps, useTheme } from '@mui/material';
import { Field, Form, Formik, FormikProps } from 'formik';
import { TextField } from 'formik-mui';
import { SchemaOf } from 'yup';

import { ManagementService } from '../services/ManagementService';
import yup from '../validation/yup';
import { Button } from './Button';
import { DialogDrawer } from './DialogDrawer';
import { Flex } from './Flex';

export type ThemeScreenProps = {
  managementService: ManagementService;
  onClose: () => void;
  onExited: () => void;
  onSave: (theme: SiteTheme) => void;
  open: boolean;
  site: Site;
};

const themeSchema: SchemaOf<SiteTheme> = yup.object().shape({
  primaryColor: yup
    .string()
    .required()
    .matches(/^#([a-f0-9]{3}|[a-f0-9]{6})$/i, {
      message: 'Please specify a valid hex colour (e.g., #4f4f4f)',
    }),
  primaryContrastText: yup
    .string()
    .required()
    .matches(/^#([a-f0-9]{3}|[a-f0-9]{6})$/i, {
      message: 'Please specify a valid hex colour (e.g., #FFF)',
    }),
});

const ColorPicker: React.FC<TextFieldProps & { value?: string }> = ({
  value,
  ...props
}) => {
  return (
    <Flex>
      <Box
        alignSelf="flex-start"
        border="solid 1px #eee"
        borderRadius="0.25rem"
        height="3rem"
        mr={1}
        mt={0.5}
        sx={{ backgroundColor: value }}
        width={'3.3rem'}
      />
      <Field
        size="medium"
        variant="outlined"
        {...props}
        component={TextField}
      />
    </Flex>
  );
};

export const EditThemeDrawer: React.FC<ThemeScreenProps> = ({
  managementService,
  onClose,
  onExited,
  onSave,
  open,
  site,
}) => {
  const theme = useTheme();
  const formRef = useRef<FormikProps<SiteTheme>>(null);
  return (
    <DialogDrawer
      actions={
        <>
          <Button onClick={() => onClose()} size="large">
            Cancel
          </Button>
          <Button
            onClick={() => formRef.current?.submitForm()}
            size="large"
            variant="contained"
          >
            Save
          </Button>
        </>
      }
      onClose={onClose}
      onExited={onExited}
      open={open}
      title="Edit theme"
    >
      <Formik
        initialValues={
          site.theme ?? {
            primaryColor: '#4f4f4f',
            primaryContrastText: '#fff',
          }
        }
        innerRef={formRef}
        onSubmit={async (siteTheme, actions) => {
          actions.setSubmitting(true);
          await managementService.updateSite(site.id, {
            site: { ...site, theme: siteTheme },
          });
          actions.setSubmitting(false);
          onSave(siteTheme);
        }}
        validationSchema={themeSchema}
      >
        {formik => (
          <Form>
            <Box my={2}>
              <ColorPicker
                fullWidth
                helperText="Used for primary button background. This is usually your primary brand color."
                label="Primary color"
                name="primaryColor"
                placeholder={theme.palette.primary.main}
                value={formik.values.primaryColor}
              />
            </Box>
            <Box my={2}>
              <ColorPicker
                fullWidth
                helperText="Text color that is displayed on top of your primary color."
                label="Primary contrast text"
                name="primaryContrastText"
                placeholder={theme.palette.primary.contrastText}
                value={formik.values.primaryContrastText}
              />
            </Box>
          </Form>
        )}
      </Formik>
    </DialogDrawer>
  );
};
