import React, { useRef, useState } from 'react';

import type { Me } from '@enactive-ac/api/src/domain/Me';
import {
  Button,
  ButtonProps,
  ClickAwayListener,
  MenuItem,
  MenuList,
  Paper,
  Popper,
} from '@mui/material';

export type AccountDropdownButtonProps = {
  me: Me;
  buttonProps?: Omit<ButtonProps, 'children' | 'ref'>;
  onSignOut: () => void;
};

export const AccountDropdown: React.FC<AccountDropdownButtonProps> = ({
  me,
  buttonProps,
  onSignOut,
}) => {
  const anchorEl = useRef<HTMLButtonElement>(null);
  const [open, setOpen] = useState(false);
  return (
    <>
      <Button
        color="inherit"
        {...buttonProps}
        ref={anchorEl}
        onClick={ev => {
          setOpen(true);
          buttonProps?.onClick?.(ev);
        }}
      >
        {me.displayName}
      </Button>
      <Popper anchorEl={anchorEl.current} disablePortal open={open}>
        <Paper>
          <ClickAwayListener onClickAway={() => setOpen(false)}>
            <MenuList>
              <MenuItem>
                <Button onClick={() => onSignOut()}>Sign Out</Button>
              </MenuItem>
            </MenuList>
          </ClickAwayListener>
        </Paper>
      </Popper>
    </>
  );
};
