import type { Account } from '@enactive-ac/api/src/domain/Account';
import {
  CreateTourRequest,
  CreateTourResponse,
} from '@enactive-ac/api/src/domain/CreateTour';
import type { GetMeResponse } from '@enactive-ac/api/src/domain/GetMe';
import type { ListSitesResponse } from '@enactive-ac/api/src/domain/ListSites';
import type { ListToursResponse } from '@enactive-ac/api/src/domain/ListTours';
import type { LoginResponse } from '@enactive-ac/api/src/domain/Login';
import type { Me } from '@enactive-ac/api/src/domain/Me';
import type {
  SignupRequest,
  SignupResponse,
} from '@enactive-ac/api/src/domain/Signup';
import type { Site } from '@enactive-ac/api/src/domain/Site';
import type { Tour } from '@enactive-ac/api/src/domain/Tour';
import type {
  UpdateSiteRequest,
  UpdateSiteResponse,
} from '@enactive-ac/api/src/domain/UpdateSite';
import axiosStatic, { AxiosInstance } from 'axios';
import { BehaviorSubject } from 'rxjs';

export class ManagementService {
  private readonly me$$ = new BehaviorSubject<Me | null | undefined>(undefined);
  private readonly sites$$ = new BehaviorSubject<Site[] | undefined>(undefined);
  private readonly tours$$ = new BehaviorSubject<Tour[] | undefined>(undefined);

  public readonly me$ = this.me$$.asObservable();
  public readonly sites$ = this.sites$$.asObservable();
  public readonly tours$ = this.tours$$.asObservable();

  constructor(
    private readonly axios: AxiosInstance = axiosStatic.create({
      baseURL: `${
        new URLSearchParams(window.location.search.slice(1)).get('_devHost') ??
        `https://api.${window.location.hostname.replace('www.', '')}`
      }/api/`,
      withCredentials: true,
    }),
  ) {}

  public async init(): Promise<void> {
    const before = this.me$$.value;
    const after = await this.getMe().catch(() => null);
    if (before?.id !== after?.id) {
      if (after === null) {
        this.sites$$.next(undefined);
      } else {
        await this.loadSites();
      }
    }
    this.me$$.next(after);
  }

  public async getMe(): Promise<Me> {
    return (await this.axios.get<GetMeResponse>('management/me')).data.me;
  }

  public async signUp(email: string): Promise<Account> {
    const req: SignupRequest = {
      account: {
        email,
        id: '',
      },
    };
    const response = await this.axios.post<SignupResponse>(
      'management/signup',
      req,
    );

    this.me$$.next({
      displayName: response.data.account.email,
      id: response.data.account.id,
      isExternal: false,
    });
    return response.data.account;
  }

  public async login(accountId: string, code: string): Promise<Account> {
    const response = await this.axios.post<LoginResponse>('management/login', {
      code,
      id: accountId,
    });
    this.me$$.next(await this.getMe());
    await this.loadSites();
    return response.data.account;
  }

  public async logout(): Promise<void> {
    await this.axios.post<Account>('management/logout');
    this.tours$$.next(undefined);
    this.sites$$.next(undefined);
    this.me$$.next(undefined);
    window.location.reload();
  }

  public async updateSite(
    siteId: string,
    req: UpdateSiteRequest,
  ): Promise<Site> {
    const response = await this.axios.put<UpdateSiteResponse>(
      `management/sites/${siteId}`,
      req,
    );
    this.sites$$.next(
      this.sites$$.value?.map(s => (s.id === siteId ? response.data.site : s)),
    );
    return response.data.site;
  }

  public async loadTours(siteId: string): Promise<Tour[]> {
    const response = await this.axios.get<ListToursResponse>(
      `management/sites/${siteId}/tours`,
    );

    this.tours$$.next(response.data.tours);
    return response.data.tours;
  }

  public async createTour(
    siteId: string,
    req: CreateTourRequest,
  ): Promise<Tour> {
    const response = await this.axios.post<CreateTourResponse>(
      `management/sites/${siteId}/tours`,
      req,
    );
    const tour = response.data.tour;
    const tours = this.tours$$.value;
    this.tours$$.next(tours === undefined ? [tour] : tours.concat([tour]));
    return tour;
  }

  public async updateTour(
    siteId: string,
    tourId: string,
    req: CreateTourRequest,
  ): Promise<Tour> {
    const response = await this.axios.put<CreateTourResponse>(
      `management/sites/${siteId}/tours/${tourId}`,
      req,
    );
    const tour = response.data.tour;
    const tours = this.tours$$.value;
    this.tours$$.next(
      tours === undefined
        ? [tour]
        : tours.map(t => (t.id === tour.id ? tour : t)),
    );
    return tour;
  }

  public async deleteTour(siteId: string, tourId: string): Promise<void> {
    const response = await this.axios.delete(
      `management/sites/${siteId}/tours/${tourId}`,
    );
    if (response.status < 300) {
      this.tours$$.next(this.tours$$.value?.filter(t => t.id !== tourId));
    }
  }

  private async loadSites(): Promise<Site[]> {
    const response = await this.axios.get<ListSitesResponse>(
      'management/sites',
    );
    this.sites$$.next(response.data.sites);
    return response.data.sites;
  }
}
