import React, { ReactElement } from 'react';

import { Upload } from '@mui/icons-material';
import { IconButton, IconButtonProps } from '@mui/material';
import { styled } from '@mui/material/styles';

export type FileImportButtonProps = {
  accept?: string;
  icon?: ReactElement;
  inputId: string;
  onImport: (file: File) => void;
  ButtonProps?: IconButtonProps;
};

const Input = styled('input')({
  display: 'none',
});

export const FileImportButton: React.FC<FileImportButtonProps> = ({
  accept,
  ButtonProps,
  icon = <Upload />,
  inputId,
  onImport,
}) => {
  return (
    <label htmlFor={inputId}>
      <Input
        accept={accept}
        id={inputId}
        onChange={async ev => {
          const file = ev.target.files?.item(0) ?? undefined;
          if (file !== undefined) {
            onImport(file);
          }
        }}
        type="file"
      />
      <IconButton {...ButtonProps} component="span">
        {icon}
      </IconButton>
    </label>
  );
};
