import React, { PropsWithChildren } from 'react';

import { ExpandMore } from '@mui/icons-material';
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Alert,
  Badge,
  Box,
  styled,
  Table,
  TableBody,
  TableCell,
  TableCellProps,
  TableHead,
  TableRow,
  Tooltip,
  Typography,
} from '@mui/material';
import { useParams } from 'react-router-dom';

import { CodeBlock } from './CodeBlock';

const CenteredCell = styled(TableCell)({
  textAlign: 'center',
});

const CodeCell: React.FC<TableCellProps> = ({ children, ...props }) => {
  return (
    <TableCell {...props}>
      <code>{children}</code>
    </TableCell>
  );
};

const CenteredCodeCell = styled(CodeCell)({
  textAlign: 'center',
});

const AttributeRow: React.FC<
  PropsWithChildren<{
    attrName: string;
    attrType: string;
    defaultValue?: string;
    example?: string;
    required?: boolean;
  }>
> = ({
  attrName,
  attrType,
  children,
  defaultValue = '',
  example,
  required = false,
}) => {
  return (
    <TableRow>
      <CenteredCell>
        <Tooltip title={required ? 'Required attribute' : 'Optional attribute'}>
          {required ? (
            <Badge badgeContent="R" color="error" />
          ) : (
            <Badge badgeContent="O" color="info" />
          )}
        </Tooltip>
      </CenteredCell>
      <CodeCell>{attrName}</CodeCell>
      <CenteredCodeCell>{attrType}</CenteredCodeCell>
      <TableCell>{defaultValue}</TableCell>
      <TableCell>
        {children}
        {example !== undefined ? (
          <>
            <Typography my={1} variant="subtitle2">
              Example:
            </Typography>
            <code>{example}</code>
          </>
        ) : undefined}
      </TableCell>
    </TableRow>
  );
};

export const InstallationScreen: React.FC = () => {
  const { siteId } = useParams<{ siteId: string }>();
  return siteId !== undefined ? (
    <Box maxWidth="md" mx="auto" my={2} width="100%">
      <Typography component="h2" variant="h5">
        Installation
      </Typography>
      <p>
        To enable Enactive Tours you must place a script into all webpages on
        which you want tours to run. Usually you will put this in a site
        template or use Google Tag Manager so it applies to all pages, and then
        you can control which URL each tour runs on in the tour configuration.
      </p>
      <p>In its simplest form, the script looks like this:</p>
      <CodeBlock
        code={trimLines(`
              |<script
              |  defer
              |  enactive.site-id="${siteId}"
              |  src="https://www.enactive.ac/integration/entrypoint.js"
              |></script>
            `)}
        language="html"
        mx="auto"
        my={2}
      />
      <p>
        Please refer to the following examples for various methods of putting
        Enactive Tours on your site.
      </p>
      <Accordion>
        <AccordionSummary expandIcon={<ExpandMore />}>
          <Typography component="h2" variant="h6">
            JavaScript
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          Copy and paste the following block of HTML and JavaScript code to the
          end of the <code>&lt;body&gt;</code> tag of your website.
          <CodeBlock
            code={trimLines(`
              |<script>
              |  const script = document.createElement('script');
              |  script.src = 'https://www.enactive.ac/integration/entrypoint.js';
              |  script.setAttribute('enactive.site-id', '${siteId}');
              |
              |  // If there is a user signed in to your website, add their User ID
              |  script.setAttribute('enactive.context.user.id', '{the user id}');
              |  document.body.appendChild(script);
              |</script>
            `)}
            language="javascript"
            mx="auto"
            my={2}
          />
        </AccordionDetails>
      </Accordion>
      <Accordion>
        <AccordionSummary expandIcon={<ExpandMore />}>
          <Typography component="h2" variant="h6">
            HTML
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Box mb={1}>
            <Alert severity="info">
              The HTML example on its own is only suitable for displaying tours
              to anonymous users. To improve our ability to remember user
              preferences, you must add a{' '}
              <code>enactive.context.user.id="{'{the user id}'}"</code>{' '}
              attribute to the script tag either in server-side rendering or
              with JavaScript.
            </Alert>
          </Box>
          Copy and paste the following block of HTML code to the end of the{' '}
          <code>&lt;body&gt;</code> tag of your website.
          <CodeBlock
            code={trimLines(`
              |<script 
              |  defer
              |  enactive.site-id="${siteId}"
              |  src="https://www.enactive.ac/integration/entrypoint.js"
              |></script>
            `)}
            language="html"
            mx="auto"
            my={2}
          />
        </AccordionDetails>
      </Accordion>
      <Typography component="h2" mt={4} variant="h5">
        Script API
      </Typography>
      <p>
        You can specify various metadata attributes on your script tag to
        control how Enactive works. Please refer to the following list for all
        metadata attributes that are available.
      </p>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell />
            <TableCell>Attribute</TableCell>
            <CenteredCell>Type</CenteredCell>
            <CenteredCell>Default</CenteredCell>
            <TableCell>Description</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          <AttributeRow
            attrName="enactive.site-id"
            attrType="string"
            example={`enactive.site-id="${siteId}"`}
            required
          >
            Your Enactive Site ID: <code>{siteId}</code>
          </AttributeRow>
          <AttributeRow
            attrName="enactive.enabled"
            attrType="boolean"
            defaultValue="true"
            example='enactive.enabled="false"'
          >
            If set to <code>false</code> then all functionality will be
            deactivated.
          </AttributeRow>
          <AttributeRow
            attrName="enactive.context.user.id"
            attrType="string"
            defaultValue="{random}"
            example='enactive.context.user.id="155987"'
          >
            Specify a User ID if a user is signed in to your website. This
            ensures that when a user "dismisses" a tour, they are not shown the
            tour again when switching devices.
          </AttributeRow>
          <AttributeRow
            attrName="enactive.context.user.locale"
            attrType="string"
            defaultValue="{derived}"
            example='enactive.context.user.locale="fr-fr"'
          >
            Specify a locale if a user is signed in to your website and has
            their own language preference. If unspecified, a preference is
            derived from the page language and the user's browser.
          </AttributeRow>
        </TableBody>
      </Table>
    </Box>
  ) : (
    <></>
  );
};

function trimLines(str: string, { end = /\s+$/, start = /^\s*\|/g } = {}) {
  return str
    .trim()
    .split('\n')
    .map(line => line.replace(start, '').replace(end, ''))
    .join('\n');
}
