import React from 'react';

import { Autocomplete, Chip } from '@mui/material';
import { TextField } from 'formik-mui';

import { PlatformCondition } from '../platforms/Platform';
import { htmlEncode } from '../util/HtmlUtil';

export type ConditionsSearchFieldProps = {
  initialConditions?: Record<string, boolean>;
  availableConditions: PlatformCondition[];
  onDataChange: (conditions: Record<string, boolean>, jsonpath: string) => void;
} & React.ComponentProps<typeof TextField>;

const OptionChip: React.FC<
  { option: PlatformCondition } & React.ComponentProps<typeof Chip>
> = ({ option, ...chipProps }) => (
  <Chip
    {...chipProps}
    color="error"
    sx={{
      ...chipProps.sx,
      backgroundColor: option.color,
    }}
  />
);

export const ConditionsSearchField: React.FC<ConditionsSearchFieldProps> = ({
  availableConditions,
  initialConditions = {},
  field,
  onDataChange,
  ...textFieldProps
}) => {
  const [selectedConditions, setSelectedConditions] =
    React.useState<PlatformCondition[]>();
  const [searchValue, setSearchValue] = React.useState('');

  React.useEffect(() => {
    if (selectedConditions === undefined) {
      setSelectedConditions(
        availableConditions.filter(c => c.id in initialConditions),
      );
    }
  }, [availableConditions, initialConditions, selectedConditions]);

  return (
    <Autocomplete
      filterOptions={(options, state) => {
        const applicableOptions = options.filter(
          o => o.applicable?.({ conditions: selectedConditions ?? [] }) ?? true,
        );
        if (state.inputValue === '') {
          return applicableOptions;
        }

        return applicableOptions
          .filter(o =>
            filterForSearch(state.inputValue, state.getOptionLabel(o)),
          )
          .sort(
            (a, b) =>
              state.getOptionLabel(a).length - state.getOptionLabel(b).length,
          );
      }}
      getOptionLabel={option =>
        `${option.label.badge}: ${option.label.description}`
      }
      multiple
      onChange={(_, value) => {
        const ids = value.reduce(
          (acc, { id }) => ({ ...acc, [id]: true }),
          {} as Record<string, boolean>,
        );
        setSelectedConditions(value);

        const jsonpath =
          value.length > 0
            ? `@.lang.and(${availableConditions
                .filter(c => c.id in ids)
                .map(c => JSON.stringify(c.expr))
                .join(',')})`
            : '';
        onDataChange(ids, jsonpath);
      }}
      options={availableConditions}
      renderInput={params => {
        const f = {
          checked: false,
          multiple: false,
          name: field.name,
          onBlur: field.onBlur,
          onChange: (ev: React.ChangeEvent<HTMLInputElement>) =>
            setSearchValue(ev.target.value),
          value: searchValue,
        };
        return <TextField {...textFieldProps} {...params} field={f} />;
      }}
      renderOption={(props, option, state) => (
        <li {...props}>
          <OptionChip
            label={option.label.badge}
            option={option}
            sx={{
              mr: 1,
            }}
          />{' '}
          <span
            dangerouslySetInnerHTML={{
              __html: highlightForSearch(
                state.inputValue,
                option.label.description,
              ),
            }}
          />
        </li>
      )}
      renderTags={(value, getTagProps, state) => {
        return (
          <>
            {value.map((c, index) => (
              <OptionChip
                {...getTagProps({ index })}
                label={state.getOptionLabel?.(c)}
                option={c}
              />
            ))}
          </>
        );
      }}
      value={selectedConditions ?? []}
    />
  );
};

function highlightForSearch(search: string, result: string): string {
  const tokens = tokenizeForRegExp(htmlEncode(search));
  return htmlEncode(result).replaceAll(
    new RegExp(`(${tokens.join('|')})`, 'gi'),
    '<strong>$1</strong>',
  );
}

function filterForSearch(search: string, result: string): boolean {
  const tokens = tokenizeForRegExp(search);
  for (const token of tokens) {
    if (!new RegExp(token, 'i').test(result)) {
      return false;
    }
  }
  return true;
}

function tokenizeForRegExp(search: string): string[] {
  return search
    .split(/\s+/g)
    .map(s => s.replace(/[-[\]{}()*+?.,\\^$|#\s]/g, '\\$&'));
}
