import React from 'react';

import type { CreateTourRequest } from '@enactive-ac/api/src/domain/CreateTour';
import type { Site } from '@enactive-ac/api/src/domain/Site';
import type { Tour } from '@enactive-ac/api/src/domain/Tour';
import { Field, FieldAttributes, FormikProps } from 'formik';
import { TextField } from 'formik-mui';

import { Platforms } from '../platforms';
import { PlatformCondition } from '../platforms/Platform';
import { ConditionsSearchField } from './ConditionsSearchField';

export type ConditionsFieldProps = {
  attrName: 'match' | 'launch';
  formik: FormikProps<CreateTourRequest['tour']>;
  site: Site;
  tour?: Tour;
  FieldProps?: {
    base?: Partial<FieldAttributes<React.ComponentProps<typeof TextField>>>;
    search?: Partial<FieldAttributes<React.ComponentProps<typeof TextField>>>;
    text?: Partial<FieldAttributes<React.ComponentProps<typeof TextField>>>;
  };
};

export const ConditionsField: React.FC<ConditionsFieldProps> = ({
  attrName,
  formik,
  site,
  tour,
  FieldProps = {},
}) => {
  const platform = Platforms[site.platform?.id ?? ''];
  if (platform?.conditions !== undefined) {
    return (
      <Field
        helperText="Search to add conditions"
        label="Conditions"
        placeholder="Begin typing to search..."
        size="medium"
        validate
        variant="outlined"
        {...FieldProps.base}
        {...FieldProps.search}
        availableConditions={platform.conditions}
        component={ConditionsSearchField}
        initialConditions={tour?.[attrName]?.data?.conditions ?? []}
        name={`${attrName}.jsonpath`}
        onDataChange={(conditions: PlatformCondition[], jsonpath: string) => {
          formik.setFieldValue(`${attrName}.data`, { conditions }, false);
          formik.setFieldValue(`${attrName}.jsonpath`, jsonpath, true);
        }}
      />
    );
  } else {
    return (
      <Field
        helperText="Enter a JSONPath boolean expression"
        placeholder={
          '!@.context.user.anonymous && @.context.page.id == "dashboard"'
        }
        size="medium"
        validate
        variant="outlined"
        {...FieldProps.base}
        {...FieldProps.text}
        component={TextField}
        name={`${attrName}.jsonpath`}
      />
    );
  }
};
