import { Platform, PlatformCondition } from './Platform';

const ultraLabels = {
  'course.role.instructor': {
    label: { badge: 'User', description: 'Is instructor in course' },
  },
  'course.role.student': {
    label: { badge: 'User', description: 'Is student in course' },
  },
  'page.id.course.calendar': {
    label: { badge: 'Page', description: 'Course calendar' },
  },
  'page.id.course.engagement': {
    label: { badge: 'Page', description: 'Course discussion' },
  },
  'page.id.course.grades': {
    label: { badge: 'Page', description: 'Course gradebook' },
  },
  'page.id.course.outline': {
    label: { badge: 'Page', description: 'Course outline' },
  },
  'page.id.stream': {
    label: { badge: 'Page', description: 'Activity stream' },
  },
  'user.anonymous.false': {
    label: { badge: 'User', description: 'Is logged in' },
  },
  'user.anonymous.true': {
    label: { badge: 'User', description: 'Is not logged in' },
  },
  'user.role.faculty': {
    label: { badge: 'User', description: 'Is primarily institution faculty' },
  },
  'user.role.student': {
    label: { badge: 'User', description: 'Is primarily institution student' },
  },
} as Record<
  string,
  {
    label: {
      badge: string;
      description: string;
    };
  }
>;

export const ultra: Platform = {
  conditions: [
    // Page conditions
    ...[
      'stream',
      'course.outline',
      'course.calendar',
      'course.engagement',
      'course.grades',
    ].map<PlatformCondition>(id => ({
      applicable: ({ conditions }) =>
        conditions.find(({ id }) => id.startsWith('page.')) === undefined,
      color: '#0002A1',
      expr: `@.context.page.id=="${id}"`,
      id: `page.id.${id}`,
      ...ultraLabels[`page.id.${id}`],
    })),
    // Auth conditions
    ...['false', 'true'].map(
      (id): PlatformCondition => ({
        applicable: ({ conditions }) =>
          conditions.find(
            ({ id }) =>
              id.startsWith('course.role.') || id.startsWith('user.anonymous.'),
          ) === undefined,
        color: '#D23369',
        expr: `${id === 'false' ? '!' : ''}@.context.user.anonymous`,
        id: `user.anonymous.${id}`,
        ...ultraLabels[`user.anonymous.${id}`],
      }),
    ),
    // Institution role conditions
    ...['faculty', 'student'].map(
      (id): PlatformCondition => ({
        applicable: ({ conditions }) =>
          conditions.find(({ id }) => id.startsWith('user.anonymous.true')) ===
          undefined,
        color: '#D23369',
        expr: `@.context.user.role=="${id}"`,
        id: `user.role.${id}`,
        ...ultraLabels[`user.role.${id}`],
      }),
    ),
    // Course role conditions
    ...['instructor', 'student'].map(
      (id): PlatformCondition => ({
        applicable: ({ conditions }) =>
          conditions.find(
            ({ id }) =>
              id.startsWith('course.role.') ||
              id.startsWith('user.anonymous.true'),
          ) === undefined,
        color: '#D23369',
        expr: `@.context.course.role=="${id}"`,
        id: `course.role.${id}`,
        ...ultraLabels[`course.role.${id}`],
      }),
    ),
    // Course state
    {
      color: '#3F0071',
      expr: `@.context.course.content.has`,
      id: 'course.state.content.has',
      label: { badge: 'Course', description: 'Has content' },
    },
    {
      color: '#3F0071',
      expr: `@.context.course.announcements.has`,
      id: 'course.state.announcements.has',
      label: { badge: 'Course', description: 'Has announcement' },
    },
    // Course activity
    {
      color: '#3F0071',
      expr: '@.activity.course.has("course.calendar.addEvent")',
      id: 'activity.course.has.createCourseEvent',
      label: {
        badge: 'Course Activity',
        description: 'Has created calendar event',
      },
    },
    {
      color: '#3F0071',
      expr: '@.activity.course.has("course.calendar.addOfficeHours")',
      id: 'activity.course.has.addOfficeHours',
      label: {
        badge: 'Course Activity',
        description: 'Has added office hours',
      },
    },
    {
      color: '#3F0071',
      expr: '@.activity.course.has("components.directives.messages.courseConversation.sendMessage.button")',
      id: 'activity.course.has.sendCourseMessage',
      label: {
        badge: 'Course Activity',
        description: 'Has sent a message',
      },
    },
    // User activity
    {
      color: '#D23369',
      expr: '@.activity.user.has("components.directives.add-content-button.contextualMenu.menuItem")',
      id: 'activity.user.has.createCourseContent',
      label: {
        badge: 'User Activity',
        description: 'Has created course content',
      },
    },
    {
      color: '#D23369',
      expr: '@.activity.user.has("course.calendar.addEvent")',
      id: 'activity.user.has.createCourseEvent',
      label: {
        badge: 'User Activity',
        description: 'Has created course event',
      },
    },
    {
      color: '#D23369',
      expr: '@.activity.user.has("course.calendar.addOfficeHours")',
      id: 'activity.user.has.addOfficeHours',
      label: {
        badge: 'User Activity',
        description: 'Has added course office hours',
      },
    },
    {
      color: '#D23369',
      expr: '@.activity.user.has("components.directives.messages.courseConversation.sendMessage.button")',
      id: 'activity.user.has.sendCourseMessage',
      label: {
        badge: 'User Activity',
        description: 'Has sent a course message',
      },
    },
  ],
};
