import React, { useEffect, useState } from 'react';

import type { Tour } from '@enactive-ac/api/src/domain/Tour';
import { Map, Download } from '@mui/icons-material';
import {
  Avatar,
  Box,
  Card,
  CardHeader,
  IconButton,
  Stack,
  styled,
} from '@mui/material';
import { useParams } from 'react-router-dom';
import { map } from 'rxjs/operators';

import { useObservable } from '../hooks/useObservable';
import { ManagementService } from '../services/ManagementService';
import { Button } from './Button';
import { EditTourDrawer } from './EditTourDrawer';
import { FeatureContainer } from './FeatureContainer';
import { FileImportButton } from './FileImportButton';
import { FirstAction } from './FirstAction';
import { Flex } from './Flex';

export type ToursScreenProps = {
  managementService: ManagementService;
};

const TourCard = styled(Card)(({ theme }) => ({
  '&.published': {
    borderLeftColor: theme.palette.success.main,
  },
  borderLeft: 'solid 3px #ccc',
}));

export const ToursScreen: React.FC<ToursScreenProps> = ({
  managementService,
}) => {
  const { siteId } = useParams<{ siteId: string }>();
  const [site] = useObservable(
    () =>
      managementService.sites$.pipe(map(ss => ss?.find(s => s.id === siteId))),
    [managementService],
  );
  const [tours] = useObservable(
    () => managementService.tours$,
    [managementService],
  );

  const [tourEditOpen, setTourEditOpen] = useState(false);
  const [tourEdit, setTourEdit] = useState<Tour>();

  useEffect(() => {
    if (siteId !== undefined) {
      void managementService.loadTours(siteId);
    }
  }, [managementService, siteId]);

  return (
    <>
      {(() => {
        if (siteId !== undefined && tours !== undefined && tours.length > 0) {
          return (
            <Box width="100%">
              <Stack direction="row" my={3} spacing={1}>
                <Button
                  color="primary"
                  onClick={() => {
                    setTourEdit(undefined);
                    setTourEditOpen(true);
                  }}
                  variant="contained"
                >
                  Create tour
                </Button>
                <FileImportButton
                  ButtonProps={{
                    title: 'Create from template',
                  }}
                  inputId="import-tour"
                  onImport={async data => {
                    const importedTour: Tour = {
                      ...JSON.parse(await data.text()),
                      id: '',
                      status: 'draft',
                    };

                    setTourEdit(importedTour);
                    setTourEditOpen(true);
                  }}
                />
              </Stack>
              <Flex flexWrap="wrap" m={-1}>
                {tours.map(tour => (
                  <Box key={tour.id} m={1} width="23%">
                    <TourCard
                      className={tour.status}
                      onClick={() => {
                        setTourEdit(tour);
                        setTourEditOpen(true);
                      }}
                      sx={{ cursor: 'pointer', position: 'relative' }}
                    >
                      <CardHeader
                        action={
                          <IconButton
                            download={`Enactive_${tour.name.replace(
                              /\s+/,
                              '_',
                            )}.json`}
                            href={`data:application/json;charset=utf-8,${encodeURIComponent(
                              JSON.stringify(tour),
                            )}`}
                            onClick={ev => ev.stopPropagation()}
                            title="Download as template"
                          >
                            <Download />
                          </IconButton>
                        }
                        avatar={
                          <Avatar sx={{ bgcolor: 'blue' }}>
                            {tour.name
                              .split(/\s+/)
                              .map(w => w[0]?.toUpperCase())
                              .join('')}
                          </Avatar>
                        }
                        subheader={
                          tour.status === 'draft' ? 'DRAFT' : 'PUBLISHED'
                        }
                        title={tour.name}
                      />
                    </TourCard>
                  </Box>
                ))}
              </Flex>
            </Box>
          );
        } else if (siteId !== undefined && tours !== undefined) {
          return (
            <FeatureContainer>
              <FirstAction
                actionsComponent={
                  <Stack direction="row" spacing={2}>
                    <Button
                      onClick={() => {
                        setTourEdit(undefined);
                        setTourEditOpen(true);
                      }}
                      size="large"
                      variant="contained"
                    >
                      Create tour
                    </Button>
                    <FileImportButton
                      ButtonProps={{
                        title: 'Create from template',
                      }}
                      inputId="import-tour"
                      onImport={async data => {
                        const importedTour: Tour = {
                          ...JSON.parse(await data.text()),
                          id: '',
                          status: 'draft',
                        };

                        setTourEdit(importedTour);
                        setTourEditOpen(true);
                      }}
                    />
                  </Stack>
                }
                illustrationComponent={<Map />}
              >
                Create your first contextual, interactive experience for your
                user that is tailored for your product or website.
              </FirstAction>
            </FeatureContainer>
          );
        }
      })()}
      {site !== undefined ? (
        <EditTourDrawer
          managementService={managementService}
          onClose={() => setTourEditOpen(false)}
          onSave={() => setTourEditOpen(false)}
          open={tourEditOpen}
          site={site}
          tour={tourEdit}
        />
      ) : undefined}
    </>
  );
};
